/* src/styles/Procedure.css */
.procedure {
    padding: 40px 20px;
    background-color: black;
    color: #333;
    text-align: center;
    border-radius: 10px;
    max-width: 800px;
    margin: 40px auto;
  }
  
  h2 {
    font-size: 28px;
    color: #8e44ad;
    margin-bottom: 20px;
  }
  
  ul {
    list-style-type: none;
    padding-left: 0;
  }
  
  ul li {
    font-size: 16px;
    margin-bottom: 10px;
    line-height: 1.5;
    color: #555;
  }
  
  ul li:before {
    content: '✔️';
    margin-right: 10px;
    color: #8e44ad;
  }
  